.controller{
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 25px;
    position: relative;
    margin-left: 10%;
    overflow: hidden;
    
   
}
.controller-card{
    display: flex;
  
}
.controller-center{
    display: flex;


}
.controller-render{

 width: 500px;;

}

.empty{
 width: 100%;
height: 21.8vh;
 

}
@media  (max-width: 768px){


    .controller{
        display: flex;
        justify-content: space-evenly;
        align-items: center;
        gap: 25px;
        position: relative;
        margin-left: 10%;
        flex-direction: column;
        
       
    }

    .controller-render{

        width: unset;
       
    
       
       }


    
}